<template>
  <div>
    <h1> Color</h1>
    <div class="color-app">

      <div class="mixed-color-patches">
        <div class="color-patch" @dragover.stop v-for="color in colors" :key="color"
          :class="{ 'hide': !color.used != undefinden & color.used }" @dragstart="(ev) => drag(ev, color)"
          :style="{ backgroundColor: color.color }" draggable="true">
        </div>
      </div>

      <div class="organized-color-patches">

        <div class="empty-box" v-for="emptyBox in emptyBoxes" :key="emptyBox.color" @dragover.prevent
          @drop="(ev) => drop(ev, emptyBox)" :class="{
            'color-verify-correctly': emptyBox.correctly & emptyBox.correctly != undefined,
            'color-verify-wrong': !emptyBox.correctly & emptyBox.correctly != undefined,
          }">
          <div class="patch-in-box" :style="{ backgroundColor: emptyBox.contain, }"> </div>
        </div>
      </div>

    </div>
    <button class="btn-verify" @click="verify"> verify </button>
  </div>
</template>


<script>



export default {
  components: {

  },
  data() {
    return {
      colors: [
        { color: '#ff5fff', used: undefined },
        { color: '#4b0f0f', used: undefined },
        { color: '#504f4f', used: undefined },
        { color: '#4d3737', used: undefined },
        { color: '#aaa9a9', used: undefined },
        { color: '#242424', used: undefined },
        { color: '#4b2929', used: undefined },
        { color: '#4c0202', used: undefined },
        { color: '#4d1c1c', used: undefined },
      ],

      emptyBoxes: [
        { color: '#ff5fff', contain: '', correctly: undefined },
        { color: '#504f4f', contain: '', correctly: undefined },
        { color: '#242424', contain: '', correctly: undefined },
        { color: '#4d3737', contain: '', correctly: undefined },
        { color: '#4b2929', contain: '', correctly: undefined },
        { color: '#aaa9a9', contain: '', correctly: undefined },
        { color: '#4d1c1c', contain: '', correctly: undefined },
        { color: '#4b0f0f', contain: '', correctly: undefined },
        { color: '#4c0202', contain: '', correctly: undefined },
      ],
    }

  },
  computed: {

  },
  methods: {
    drag(ev, color) {
      ev.dataTransfer.setData('color', color.color);

    },
    drop(ev, emptyBox) {
      emptyBox.contain = ev.dataTransfer.getData('color');
      console.log(emptyBox.contain)


    },
    verify() {
      this.emptyBoxes.forEach((item) => { if (item.color === item.contain) { item.correctly = true } else { item.correctly = false } })
      console.log(this.emptyBoxes)
    }

  },
  emits: {
  },
}
</script>

